<template>
	<view class="wechatItem">
		<!-- 病例，只有群组里才有 -->
		<view class="header" justify="center" v-if="caseid">
			<u-row gutter="16" class='headerRow' @click='enterCase(caselist.caseHistoryId)'>
				<u-col span="3">
					<view class="caseimage">
						<u-image v-if='caselist.previewImage' width="100rpx" height="100rpx" :show-error='false' :src="caselist.previewImage" mode="aspectFit"></u-image>
						<u-image v-else width="100rpx" height="100rpx" src="/static/image/shoyiilogo.png" mode="aspectFit"></u-image>		
					</view>
				</u-col>
				<u-col span="4">
					<view class="case">
						<view class="content">
							<text>患者姓名：</text>
							<text>{{caselist.name}}</text>
						</view>
						<view class="content">
							<text>病历名称：</text>
							<text>{{caselist.caseHistoryName}}</text>
						</view>
					</view>
				</u-col>
				<u-col span="5">
					<view class="case">
						<view class="content">
							<text>病历号：</text>
							<text>{{caselist.caseHistoryNo}}</text>
						</view>
						<view class="content">
							<text>创建时间：</text>
							<text>{{caselist.createTime}}</text>
						</view>
					</view>
				</u-col>
			</u-row>
		</view>
		<scroll-view scroll-top='50' :scroll-into-view="chatIndex" :scroll-with-animation="true" scroll-y="true" @scrolltoupper='toupper' upper-threshold="50" class="section">
			<u-loadmore :status="status" :is-dot='true' :load-text="loadText"/>
			<view class="section-item" >
				<view v-for="(item,index) in lastestChat" :key='index'>
					<text class="time" v-if="index-1 >= 0 ? lastestChat[index-1].sendTime < item.sendTime - 300000 : true" style="">{{item.sendTime|relativtime}}</text>
					<view style="display: flex;" class="chatlist" :class="item.userId==userId?'alluser':'allother'" :id="'chatIndex'+index">
						<u-image class='image' style='border: 1px solid #F8F8F8;' width='80' height='80'  :src="item.userProfile" mode='aspectFit'>
							<view slot="error" style="">
								<u-image width='80rpx' height='80rpx' src='/static/image/person.png' mode="aspectFit"></u-image>
							</view>
						</u-image>
						<view style="color: #000000;max-width: 500rpx;" :class="item.userId==userId?'userCord':'otherCord'">
							<!-- 文本信息 -->
							<view :class="item.userId==userId?'TextuserCord':'TextotherCord'" v-if="item.msgType=='1'">
								<text selectable='true'>{{item.content}}</text>
							</view> 
							<!-- 方法库数据 -->
							<view @click="enterDetail(JSON.parse(item.content).surgicalPlanId)" class="surgicalCode" v-if="item.msgType=='3'">
								<view class="s_header" style="">
									<text>{{JSON.parse(item.content).surgicalPlanName}}</text>
								</view>
								<view class="s_section" style="">
									<text class="content" style="">{{JSON.parse(item.content).title}}</text>
									<image class="image" v-if="JSON.parse(item.content).previewImage" :src="JSON.parse(item.content).previewImage" mode="aspectFit"></image>
									<image class="image" v-else src="/static/image/shoyiilogo.png" mode="aspectFit"></image>
								</view>
								<view class="s_footer" style="">
									<image class="image" style="" src="/static/image/shoyiilogo.png" mode="aspectFit"></image>
									<text class="name">{{JSON.parse(item.content).doctorName}}</text>
								</view>
							</view>
							<!-- 图片 -->
							<view class="cordimages" v-if="item.msgType=='2'" @click="previewImage(item,index)">
								<u-image class="chatimage" height="300rpx" :src="item.content" mode="aspectFill"></u-image>
							</view>
							<view class="cordfile" v-if="item.msgType=='4'" @click="openFile(JSON.parse(item.content).fileDownloadUrl)">
								<view class="title" style="">
									<text>{{JSON.parse(item.content).name}}</text>
								</view>
								<view class="" v-if="filesuffix(item)=='mp3'">
								    <u-icon name="play-circle-fill" size='100' color='#86B0D4'></u-icon>
								</view>
								<view class=""
								      v-else-if="filesuffix(item)=='pdf'||filesuffix(item)=='zip'||filesuffix(item)=='docx'">
								    <u-icon name="file-text-fill" size='100' color='#fa3534'></u-icon>
								</view>
								<view class="" v-else>
								    <u-icon name="file-text-fill" size='100' color='#999'></u-icon>
								</view>
							</view>
						</view>
						<view class="" style="width: 80rpx;"></view>
					</view>
				</view>
			</view>
		</scroll-view>
		<l-file ref="lFile" @up-success="onSuccess"></l-file>
		<view class="footer">
			<view class="">
				<u-field
					:clearable='false'
					@focus='Inputfocus'
					class='input'
					label=''
					label-width='0'
					v-model="InputContent"
					type="textarea"
					:field-style='fieldStyle'
					maxlength='250'
					:border-bottom='false'
					placeholder-style="background: #f8f8f8;border-radius:36rpx;"
				>
				<view class="" slot="right" class='sendBtn' @touchend.prevent="sendItem(0)">
					<text>发送</text>
				</view> 
				</u-field>
			</view>
			<view class="sendFile">
				<text @click="sendItem(1)" class="iconfont icontupian"></text>
				<text @click="sendItem(2)" class="iconfont iconwenjian"></text>
				<text @click="sendItem(3)" class="iconfont iconshoushufangfa"></text>
			</view>
		</view>
		
		<surgical-popup ref='Sonshow' :chatId='chatId' :type='type' @addPage='addPage' :page="page" :total="total" :otherId='otherid' :surgical='surgical' @refleshData='refleshData'></surgical-popup>
	</view>
</template>

<script>
	import {sendMessage,findchatLastest} from '@/utill/api/connect/connect.js'
	import {getcasedetail} from '@/utill/api/case/getcasedetail.js'
	import {relativtime} from '@/utill/tools/timestamp.js'
	import {getSurgical} from '@/utill/api/surgical/getSurgical.js'
	import {mapGetters,mapState} from 'vuex' 
	import surgicalPopup from './components/surgicalPopup.vue'
	import {getFileExtension} from '@/utill/tools/filesuffix.js'
	export default{
		components:{
			surgicalPopup,
		},
		data(){
			return{
				status:'nomore',
				loadText: {
					loadmore: '轻轻下拉',
					loading: '',
					nomore: '没有更多' //默认是圆点
				},
				otherid:null,//其他人id
				userId:uni.getStorageSync('doctorId'),
				caseid:'',
				InputContent:'',//输入的内容
				fieldStyle:{
					backgroundColor:'#fffff',
				},
				lastestChat:[],
				page:1,
				total:1,//总页数
				mark:0,
				surgical:[],//手术方法库数据
				type:null,
				caselist:{},
				chatIndex:'',
				chatId:'',//每一条记录的id
				pictureid:'',
				fileId:'',//上传文件的id
			}
		},
		watch:{
			'socketData': function(val) {
				console.log('watch聊天时监听')
				console.log(val)
				if(val=='head'){//心跳回复
					
				}else if(JSON.parse(val).friend_request){//请求的通知
					console.log(JSON.parse(val).friend_request)
				}
				else{
					this.InputContent=''
					console.log(JSON.parse(val))
					console.log(JSON.parse(val).chatMsgDetail)
					var receiveData=JSON.parse(val).chatMsgDetail
					console.log(this.lastestChat)
					console.log(receiveData)
					if(this.chatId==receiveData.chatId){
						this.lastestChat.push(receiveData)
					}
					setTimeout(()=>{ //滚动底部
						this.pageToBottom();
					},300)
				}
			}
			
		},
		computed:{
			...mapGetters(['connect']),
			...mapState(['socketData'])
		},
		
		filters:{
			relativtime,
		},
		methods:{
			// 文件尾缀获取
			filesuffix(data) {
			    if (JSON.parse(data.content).fileDownloadUrl && JSON.parse(data.content)) {
			        getFileExtension(JSON.parse(data.content).fileDownloadUrl)
			        // console.log(getFileExtension(JSON.parse(data.content).fileDownloadUrl))
			        var dataBool = getFileExtension(JSON.parse(data.content).fileDownloadUrl)
			        return dataBool;
			    } else {
			        return
			    }
			},
			// 上传成功
			onSuccess(res) {
				console.log(res)
				console.log(res.fileName)
				console.log('上传成功回调',JSON.parse(res.data.id));
				if(JSON.parse(res.data.id).code==0){
					this.fileId=JSON.parse(res.data.id).object
				}else{
					this.fileId='';
					return;
				}
				console.log(this.fileId)
				console.log(this.fileId)
				if(!this.fileId){
					uni.showToast({
						title:'文件没上传',
						icon:'none'
					})
					return;
				}
				var msg={
					chatId:this.chatId,
					sendUserType:'D',
					sendUserId:this.userId,
					receiveUserType:this.type,
					receiveUserId:this.otherid,
					msgType:'4',
					msgContent:String(this.fileId)
				}
				console.log(msg)
				this.$store.dispatch('WEBSOCKET_SEND',JSON.stringify(msg))
				// uni.sendSocketMessage({
				//   data: JSON.stringify(msg),
				//   success:(e)=>{
				// 	  console.log(e)
				// 	  setTimeout(()=>{ //滚动到底部
				// 		this.pageToBottom();
				// 	  },300)
				//   },
				//   fail(err) {
				// 	console.log(err)
				// 	uni.showToast({
				// 		title:'聊天连接已中断，请重新开启App',
				// 		icon:'none'
				// 	})
				//   }
				// });		
			},
			// 图片预览
			previewImage(item,index){
				var arr=[]
				if(item.msgType=='2'){
					arr.push(item.content)
				}
				console.log(arr)
				uni.previewImage({
					current:index,
					urls:arr,
					success(e){
						console.log(e)
					},
					fail(err){
						console.log(err)
					}
				})
			},
			pageToBottom(){
				let lastIndex = this.lastestChat.length - 1
				if(lastIndex < 0) return;
				this.chatIndex = 'chatIndex'+lastIndex
			},
			// 群组的病例详情
			enterCase(id){
				uni.navigateTo({
					url:'/pages/caselist/caselistdetail?id='+id,
					animationType:'pop-in'
				})
			},
			// 手术方法库页面新增
			addPage(){
				console.log(this.page)
				this.mark=1
				this.getSurgical(this.mark,this.page)
			},
			toupper(e){//向上刷新
				// 查找历史记录
				console.log(this.page)
				console.log(this.total)
				if(this.page >= this.total) return this.status = 'nomore';
				this.status = 'loading';
				this.page++;
				this.mark=1
				if(this.type=='GROUP'){//群组
					this.fondLast(this.mark,this.page,'GROUP')	
				}else{
					this.fondLast(this.mark,this.page,'D')
				}
			},
			Inputfocus(){
				// #ifdef H5
				console.log('h5')
				// #endif
				// #ifndef H5 
				// 获取键盘光标位置
				this.pageToBottom();
				  // setTimeout(()=>{ //滚动到底部
				  // 	this.pageToBottom();
				  // },300)
				 
				// #endif
			},
			// 发送文字，图片,文件,手术方法
			sendItem(index){
				console.log(this.type)
				console.log(this.$store.state.socketTask)
				if(!this.$store.state.is_open_socket){
					uni.showToast({
						title:'连接已断，请重新启动App',
						icon:'none'
					})
					return;
				}
				if(this.type=='GROUP'){//群组消息
					var type='GROUP'
				}else{
					var type='D'
				}
				if(index==0){//发送文本
					if(!this.InputContent){
						uni.showToast({
							title:'内容不能为空',
							icon:'none'
						})
						return;
					}
					var reg = /[^\u0020-\u007E\u00A0-\u00BE\u2E80-\uA4CF\uF900-\uFAFF\uFE30-\uFE4F\uFF00-\uFFEF\u0080-\u009F\u2000-\u201f\u2026\u2022\u20ac\r\n]/g;
					if(this.InputContent.match(reg)) {
						uni.showToast({
							title:'不能发送表情',
							icon:'none'
						})
						return;
					}
					var msg={
						chatId:this.chatId,
						sendUserType:'D',
						sendUserId:this.userId,
						receiveUserType:type,
						receiveUserId:this.otherid,
						msgType:'1',
						msgContent:this.InputContent
					}
					console.log(msg)
					this.$store.dispatch('WEBSOCKET_SEND',JSON.stringify(msg))
				}else if(index==1){
					var that=this
					uni.chooseImage({
					    count: 3, //默认9
					    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					    success(res) {
							console.log(res)
							const tempFilePaths = res.tempFilePaths;
							const tempFiles=res.tempFiles;
							tempFilePaths.forEach(item=>{
								uni.uploadFile({
									url:that.$uploadUrl+'/cosFile/uploadSingleOpenImage',
									method:'POST',
									filePath: item,
									formData: {
										'compression': true  //formData是指除了图片以外，额外加的字段
								    },
									name: 'file',//注意是file还是files
									success: (res) => {
										console.log(res.data);
										console.log(JSON.parse(res.data).object)
										that.pictureid=JSON.parse(res.data).object
										var msg={
											chatId:that.chatId,
											sendUserType:'D',
											sendUserId:that.userId,
											receiveUserType:type,
											receiveUserId:that.otherid,
											msgType:'2',
											msgContent:String(that.pictureid)
										}
										console.log(msg)
										that.$store.dispatch('WEBSOCKET_SEND',JSON.stringify(msg))
									},
									fail:(err)=>{
										console.log(err)
									}
								})
							})
					    },
						fail(err) {
							console.log(err)
						}
					});
				}else if(index==2){//文件
					/**
					 * currentWebview: 当前webview
					 * url：上传接口地址
					 * name：附件key,服务端根据key值获取文件流，默认file,上传文件的key
					 * header: 上传接口请求头
					 */
					this.$refs.lFile.upload({
						// #ifdef APP-PLUS
						// nvue页面使用时请查阅nvue获取当前webview的api，当前示例为vue窗口
						currentWebview: this.$mp.page.$getAppWebview(),
						// #endif
						url: this.$uploadUrl+'/cosFile/uploadSingleChatFile',
						// name: 'myFile',
						// header: {'Authorization':'token'},
						// 其他业务参数直接写key,value,如：
						// key1: 'value1',
						// key2: 'value2',
					});
					
				
				}else{//手术方法库
					console.log(this.surgical)
					if(this.surgical.length!=0){
						this.$refs.Sonshow.open()
					}else{
						this.getSurgical(0,1)
					}
					setTimeout(()=>{
						this.pageToBottom();
					},300)
				}
			},
			// 手术方法库详情
			enterDetail(id){
				uni.navigateTo({
					url:'/pages/surgicalData/surgicalDetail?id='+id
				})
			},
			// 文件详情
			openFile(url){
				console.log(url)
				uni.showLoading({
					title:'打开中',
					icon:'none'
				})
				uni.downloadFile({
					url:url,
					success(res){
						console.log(res)
						if (res.statusCode === 200) {
							console.log('下载成功');
							uni.hideLoading()
								//打开文档查看
							uni.openDocument({
								filePath: res.tempFilePath,
								success: function(res) {
									console.log('打开文档成功');
								}
							});
						}else{
							uni.hideLoading()
							console.log('下载失败');
						}
					},
					fail(err){
						uni.hideLoading()
						console.log(err)
					}
				})
			},
			// 查询最新历史记录
			fondLast(mark,page,type){
				console.log(type)
				var that=this
				findchatLastest({
					sendUserType:'D',
					sendUserId:this.userId,
					receiveUserType:type,//目标类型
					receiveUserId:this.otherid, //目标用户(或者群组id)
					pageNum:page,
					pageSize:10,
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						if(res.data.object){
							this.total=res.data.object.pages//总页数
							if(mark==0){
								this.lastestChat=res.data.object.list.reverse()
							}else if(mark==1){
								this.lastestChat=this.lastestChat.reverse().concat(res.data.object.list).reverse()
							}
							console.log(this.lastestChat)
						}
					}else if(res.data.code==-1){//数据为空
						this.status='nomore'
					}
					else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 查询相关病例
			caseData(caseid){
				getcasedetail({
					caseHistoryId:caseid
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						var data=res.data.object
						this.caselist=res.data.object
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 方法库发送之后,要重新加载聊天记录
			refleshData(){
				setTimeout(()=>{ //滚动到底部
					this.pageToBottom();
				},300)
			},
			// 获取手术方法
			getSurgical(mark,page){
				getSurgical({
					start:page,
					length:6,
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.total=res.data.object.pages//总页数
						this.page++//页数增加一页
						if(mark=='1'){//加载更多的操作
							this.surgical=this.surgical.concat(res.data.object.list) 
						}else{//初始化操作
							this.surgical=res.data.object.list
						}
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
		},
		// 导航栏右上角
		onNavigationBarButtonTap(e){
			console.log(e)
			console.log(this.type)
			if(this.type=='GROUP'){//进入群聊设置
				uni.navigateTo({
					url:'/pages/shoyiiChat/contactGroup?groupId='+this.otherid
				})
			}else{//进入私聊设置
				
			}
		},
		onLoad(options) {
			console.log(options)
			this.otherid=options.id	//群组或私聊id
			this.type=options.type
			this.chatId=options.chatId
			if(options.type=='GROUP'){//群组,这个是从聊天列表进来的
				// 查询相关病例
				if(options.caseId!='undefined'){
					this.caseid=options.caseId
					this.caseData(options.caseId)
				}
				uni.setNavigationBarTitle({//显示群组名称
					title:options.otherName
				})
				this.fondLast(0,1,'GROUP')
			}else if(options.type=='D'){//私聊
				uni.setNavigationBarTitle({
					title:options.otherName
				})
				// 查询最新聊天记录
				this.fondLast(0,1,'D')
			}else{
				uni.setNavigationBarTitle({
					title:options.otherName
				})
				// 查询最新聊天记录
				this.fondLast(0,1,'MGR')
			}
			setTimeout(()=>{ 
				this.pageToBottom();
			},300)
			// 获取手术方法库
			this.getSurgical(0,1)
		},
	}
</script>

<style scoped lang="scss">
		.cordimages {
			width: 300rpx;
			height: 300rpx;
			display: flex;
			align-items: flex-start;
			.chatimage{
				width: 300rpx;
				height: 300rpx;
				&.u-image__image{
					width: 300rpx!important;
					height: 300rpx!important;
				}
			}
		}
		.image /deep/ uni-image{
			width: 80rpx!important;
			height: 80rpx!important;
		}
		page{
			width: 100%;
			height: 100%;
		}
		.wechatItem{
			width: 100%;
			/* #ifdef H5 */
			height:100%;
			/* #endif */
			/* #ifndef H5 */
			height: 100vh;
			/* #endif */
			display: flex;
			flex-direction: column;
		}
		.header{
			padding: 0 30rpx;
			height: auto;
			margin-top: 20rpx;
			.headerRow{
				background-color: #FFFFFF;
				.caseimage{
					border: 1px solid #F8F8F8;
					display: flex;
					justify-content: center;
					.u-image{
						text-align: center;
						max-height: 140rpx;
					}
				}
				.case {
